.btn{
    position: absolute;
    bottom: 4rem;
    right: 0;
    div{
        display: flex;
    align-items: center;
    }

    &__text{
        font-size: 1.6rem;
        text-transform: uppercase;
        font-family: 'Comfortaa', cursive;
    }

    svg{
        font-size: 2.6rem;
    color: var(--dark-color);
    }

    span{
        width: 70%;
        aspect-ratio: 1/1;
        z-index: -1;
        border-radius: 50%;
        border: 1px solid var(--slight-dark-color);
        top: 40%;

    }
}