@use "./forwarding";
/* start general css */
:root {
  /*  change  */
  --slight-primary-color: hsl(152, 100%, 85%);
  --primary-color: hsl(152, 100%, 48%);
  --dark-primary-color: hsla(152, 100%, 43%);
  --darkest-primary-color: hsla(152, 100%, 35%);
  --dark-color: hsl(0, 0%, 34%);
  --slight-dark-color: rgba(86, 86, 86, 0.3);
  --very-slight-dark-color: hsl(0, 0%, 95%);
  --text-color: hsl(0, 0%, 69%);
  --white-color: #fff;
  --slight-white-color: rgba(255, 255, 255, 0.8);
  --black-color: #000;
  --notFound-back-color: rgba(102, 100, 46, 0.8);
  --primary-curve: cubic-bezier(0.62, 0.05, 0.01, 0.99);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

html {
  font-family: "Maven Pro", sans-serif;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  cursor: url(./assets/imgs/mouse.png), auto !important;

  
}
html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased !important;

  &::-webkit-scrollbar {
    background-color: var(--slight-dark-color);
    border-radius: 5px;
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--primary-color);
    border-radius: 5px;
    cursor: grab;
  }
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
  display: inline-block;
  background-color: transparent;
}

ul {
  list-style: none;
}

select,
input,
textarea,
button {
  background-color: none;
  outline: none;
  font: inherit;
}
select:active,
input:active,
textarea:active,
button:active {
  outline: none;
}

button {
  overflow: visible;
  cursor: pointer;
  background: none;
  border: 0;
}

textarea {
  resize: vertical;
  overflow: auto;
}

span {
  display: inline-block;
}

input,
textarea,
select {
  padding: 0.5rem 1rem;
  width: 100%;
}

input:not([type="submit"]),
textarea {
  font-size: 1.4rem;
}

input[type="submit"] {
  cursor: pointer;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

img,
picture {
  max-width: 100%;
  display: block;
  border: none;
}

// p {
//   width: clamp(45ch, 50%, 75ch);
// }

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.8rem;
}

/* ∞∞ IE ∞∞ */
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

mark {
  background: #ff0;
  color: #000;
}

svg:not(:root) {
  overflow: hidden;
}

legend {
  border: none;
  padding: 0;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

/* ∞∞ moz ∞∞ */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: none;
  padding: 0;
}

blockquote {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
}

fieldset {
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  -webkit-padding-before: 0;
  -webkit-padding-start: 0;
  -webkit-padding-end: 0;
  -webkit-padding-after: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration,
progress {
  -webkit-appearance: none;
}

/* start helper classes */
.paddB_35 {
  padding-bottom: 3.5rem;
}

.paddTB_35 {
  padding: 3.5rem 0;
}
.paddT_65 {
  padding-top: 65px;
}
.top_65 {
  top: 65px;
}
.fullHeight {
  min-height: 100vh;
}
.error {
  color: red;
  font-size: 1.3rem;
}
.notFullHeight {
  min-height: calc(100vh - 65px);
}
.overlay {
  color: #fff;
  position: relative;
  z-index: 1;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(rgba(0, 0, 0, 0.7)),
      color-stop(30%, rgba(9, 9, 9, 0))
    );
    z-index: -1;
  }
}

.centerAbsolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.centerFlex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centerGrid {
  display: grid;
  place-content: center;
}

.relative {
  position: relative;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

/* end helper classes */
/* end general css */

/* /////////GENERAL///////// */
.heading {
  margin-top: 9rem;
  font-family: "Comfortaa", cursive;
  text-transform: uppercase;
  text-align: center;

  span {
    position: relative;
    @include forwarding.stroked($color: #fff, $strokeColor: var(--dark-color));
    &::after {
      content: "";
      position: absolute;
      top: 60%;
      transform: translateY(-50%);
      z-index: -1;
      border-radius: 50%;
      background-color: var(--slight-primary-color);
    }
  }

  &_big {
    span {
      font-size: 7rem;
      &::after {
        width: 30rem;
        height: 30rem;
      }
    }
  }
  &_right {
    span {
      &::after {
        right: -15rem;
      }
    }
  }
  &_small {
    span {
      font-size: 4rem;
      &::after {
        width: 20rem;
        height: 20rem;
      }
    }
  }
  &_left {
    span {
      &::after {
        left: -10rem;
      }
    }
  }
}
.container,
.nav_container {
  width: clamp(200px, 100%, 1800px);
  display: grid;
  grid-template-columns: 100px 1fr 100px;
}
.nav_container {
  left: 50%;
  position: fixed;
  z-index: 3;

  & > *:first-child {
    grid-column: 2/3;
  }
}
.container {
  margin: 0 auto;
  overflow: hidden;
  & > *:first-child {
    grid-column: 2/3;
  }
}

.cornerPosition {
  position: absolute;
  z-index: -1;
}

.bottomRight {
  bottom: -90px;
  right: -90px;
}
.topLeft {
  // position: fixed;
  top: -90px;
  left: -90px;
}

.circularHeading {
  font-size: 2rem;
  color: var(--black-color);
  position: relative;
  margin-left: 3rem;
  text-transform: capitalize;
  margin-bottom: 5rem;

  &::after,
  &::before {
    border: 1px solid var(--primary-color);
    position: absolute;
    content: "";
    border-radius: 50%;
    top: 50%;
    z-index: -1;

    transform: translateY(-50%);
  }

  &::after {
    width: 6rem;
    height: 6rem;
    left: -3rem;
  }

  &::before {
    width: 3rem;
    height: 3rem;
    left: -1.5rem;
  }
}

.fullModal {
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 4;

  .modal {
    position: absolute;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: -1;
    background-color: var(--slight-white-color);

    &.yellowModal {
      background-color: var(--notFound-back-color);
    }
    &.darkModal {
      background-color: rgba(0, 0, 0, 0.7);
    }
  }
}

.input_wrap {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.7rem;
  label {
    font-size: 1.4rem;
    margin-bottom: 0.3rem;
    text-transform: uppercase;
    font-weight: 500;
    color: var(--dark-color);
  }
  input,
  textarea {
    border-radius: 3px;
    color: var(--dark-color);
    border: 1px solid var(--slight-dark-color);
    padding-left: 25px;
  }
  ::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: var(--text-color);
  }
  ::-moz-placeholder {
    /* Firefox 19+ */
    color: var(--text-color);
  }
  :-ms-input-placeholder {
    /* IE 10+ */
    color: var(--text-color);
  }
  :-moz-placeholder {
    /* Firefox 18- */
    color: var(--text-color);
  }
  span {
    color: red;
  }
  p {
    margin-top: 0.3rem;
  }
  textarea {
    max-height: 15rem;
  }
}
/* /////////contact///////// */
.inputAndIcon {
  position: relative;
  &__icon {
    position: absolute;
    color: var(--slight-dark-color);
    font-size: 1.7rem;
    left: 5px;
    top: 6px;
  }
}

/* /////////queries///////// */
@media (max-width: 930px) {
  html {
    font-size: 8.7px;
  }
  .container,
  .nav_container {
    grid-template-columns: 6vw 1fr 6vw;
  }
  .notFullHeight {
    min-height: calc(100vh - 56.67px);
  }

  .paddT_65 {
    padding-top: 56.67px;
  }
  .paddT_65 {
    padding-top: 56.67px;
  }
  .top_65 {
    top: 56.67px;
  }
}
@media (max-width: 770px) {
  .heading_right span::after {
    right: -6rem;
  }
  .heading_left span::after {
    left: -6rem;
  }
  .heading_big span::after {
    width: 20rem;
    height: 20rem;
  }
  .heading_small span::after {
    width: 18rem;
    height: 18rem;
  }
}
@media (max-width: 700px) {
  .nav_container {
    grid-template-columns: 3vw 1fr 3vw;
  }
}

@media (max-width: 630px) {
  .heading_big span {
    font-size: 5rem;
  }
  .heading_small span {
    font-size: 3rem;
  }
}
@media (max-width: 525px) {
  .heading_big {
    span {
      font-size: 3.2rem;
      &::after {
        width: 10rem;
        height: 10rem;
      }
    }
  }
  .heading_small {
    span {
      font-size: 2rem;
      &::after {
        width: 8rem;
        height: 8rem;
      }
    }
  }
  .heading span {
    &::after {
      left: auto;
      right: 50%;
      top: 50%;
      transform: translate(50%, -50%);
    }
  }
}
