.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0 1.5rem;
  border-bottom: 1px solid var(--dark-color);
  // background-color: var(--white-color);
}

.whiteNav {
  border-bottom-color: #fff;
  .burger {
    span {
      background-color: #fff;
    }
  }

  .navBtn {
    color: #fff;
    // &:last-child svg path{
    //       fill: #fff;
    // }
    &:first-child svg path {
      stroke: #fff;
    }
  }
}

.burger {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 0.5rem;
  span {
    display: block;
    height: 2px;
    background-color: var(--dark-color);
    transition: 0.5s ease-in-out;

    &:last-of-type {
      transform-origin: 39% center;
      width: 4rem;
    }
    &:first-of-type {
      transform-origin: 31.3% center;
    }
  }

  &__top {
    &_on {
      width: 4rem;
      transform: rotate(45deg);
    }
    &_off {
      width: 2rem;
      transform: rotate(0deg);
    }
  }

  &__bottom {
    &_on {
      transform: rotate(-45deg);
    }
    &_off {
      transform: rotate(0deg);
    }
  }
}

.navBtn {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: var(--dark-color);
  gap: 5px;
  font-weight: 500;

  &:first-child svg path {
    stroke: var(--dark-color);
  }

  &:last-child svg {
    width: 27px;
  }
}
/* //////////queries////////// */
@media (max-width: 370px) {
  .burger {
    span:last-of-type {
      width: 3rem;
    }
    span:first-of-type {
      transform-origin: 18.3% center;
    }
    &__top_on {
      width: 3rem;
    }
    &__top_off {
      width: 1.5rem;
    }
  }
  .navBtn {
    font-size: 1.4rem;
  }
}
