@use "../../forwarding";

.theCase {
  position: relative;

  &__back {
    padding-top: clamp(1.8rem, 17rem, 23vh);
    background-position: 50% 50%, 0 0;
    background-size: cover, auto;
    background-repeat: no-repeat;
  }

  &__title {
    letter-spacing: 0.3rem;
    @include forwarding.stroked(
      $color: transparent,
      $strokeColor: #fff,
      $fontSize: clamp(4rem, 5rem, 6vh)
    );
    text-transform: uppercase;
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 1.5rem;
    margin-bottom: 5.6rem;
  }

  header > div {
    padding-bottom: 3rem;
  }

  &__link {
    float: right;
  }
}

.props {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.6rem;
    text-transform: capitalize;

    span {
      color: var(--dark-color);
    }
  }
  &__colors,
  &__technologies {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
  }

  // html,
  // body {
  //   background-color: #fff;
  // }

  &__color {
    width: 7.6rem;
    height: 7.6rem;
    cursor: pointer;
    background-color: #fff;

    &,
    & > div {
      border-radius: 50%;
      border: 1px solid;
    }
    & > div {
      width: 70%;
      height: 70%;
      font-weight: 500;
      // color: #fff;

      &:hover span {
        display: block;
      }
      div {
        // mix-blend-mode: difference;
        flex-direction: column;
        height: 100%;
      }

      span {
        display: none;
        font-size: 2rem;
        line-height: 1;
      }
    }
  }

  &__technology {
    padding: 1rem 2rem;
    border-radius: 60px;
    text-transform: uppercase;
    font-size: 1.5rem;
  }

  ///
}

.mindset,
.preparation {
  img {
    border: 1px solid var(--very-slight-dark-color);
    border-radius: 10px;
  }
}
.steps {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}
.step {
  position: relative;
  padding-left: 2rem;

  img:first-of-type {
    aspect-ratio: 2 /1;
  }

  // &:not(:last-of-type) {
  //   margin-bottom: 7rem;
  // }

  p {
    font-size: 1.7rem;
    margin-bottom: 2rem;
  }
  span {
    // color: var(--primary-color);
    @include forwarding.stroked(
      $color: var(--primary-color),
      $strokeColor: var(--dark-color),
      $fontSize: 1.9rem
    );
    position: absolute;
    left: 0;
    top: -2rem;
  }
}

.story {
  &__content {
    display: flex;
    gap: 1.7rem;

    & > div {
      flex-basis: 50%;
    }
  }
  &__bold {
    font-weight: 700;
  }

  &__img {
    img {
      margin: -9rem auto;
      width: clamp(280px, 400px, 90%);
      filter: drop-shadow(2px 4px 8px #585858);
    }
  }

  &__text {
    font-size: 1.7rem;

    span {
      text-decoration: underline;
      position: relative;
      display: inline-block;

      &:hover img {
        display: block;
      }
      img {
        display: none;
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        min-width: 200px;
      }
    }
  }

  &__bold {
    margin: 2rem 0 0.8rem;
  }
}
.inspirations {
  &__content {
    display: flex;
    flex-direction: column;
    gap: 1.4rem;
    a {
      font-size: 1.7rem;
      text-transform: capitalize;
      font-weight: 800;
      color: var(--dark-color);
      align-self: flex-start;
      display: flex;
      align-items: center;
      gap: 5px;

      &:hover {
        text-decoration: underline;
      }

      svg {
        color: var(--primary-color);
      }
    }
  }
}

.inspirations {
  padding-bottom: 10rem;
}
/* ///////////queries//////////// */
@media (max-width: 550px) {
  .theCase {
    &__title {
      font-size: clamp(2rem, 3rem, 4vh);
    }
    &__subtitle {
      font-size: 1.4rem;
    }
  }
}
/* /////////queries///////// */
@media (max-width: 750px) {
  .props {
    &__top {
      align-items: flex-start;
      gap: 1rem;
      flex-direction: column;
    }

    &__colors {
      flex-wrap: wrap;
    }
  }

  .steps {
    display: block;
  }

  .step{
    &:not(:last-of-type) {
        margin-bottom: 7rem;
      }
  }

  .story {
    &__img {
      img {
        margin: -2rem auto;
      }
    }
  }
}
@media (max-width: 580px) {
  .story {
    &__content {
      flex-direction: column;

      

      & > div {
        flex-basis: auto;
      }
    }

    &__img {
      img {
        margin: 2rem auto;
        width: clamp(180px, 200px, 80%);
      }
    }
  }
}
