/* ////////////////////////// */
@use  '../../forwarding';
.navigation {
  padding: 1rem 0 0;
  position: fixed;
  background-color: var(--slight-white-color);
  left: 0;
  right: 0;

  &__item {
    &:not(:last-of-type){
      margin-bottom: 1.6rem;
    }

    a {
      position: relative;
      @include forwarding.stroked;
      &.active {
        color: var(--dark-color);
      }
      &:not(.active):hover {
        color: var(--text-color);
        text-shadow: var(--text-color) 0px 0px 3px,
          var(--text-color) 0px 0px 3px, var(--text-color) 0px 0px 3px,
          var(--text-color) 0px 0px 3px, var(--text-color) 0px 0px 3px,
          var(--text-color) 0px 0px 3px;

          span{
              text-shadow: var(--text-color) 0px 0px 1px,
          var(--text-color) 0px 0px 1px, var(--text-color) 0px 0px 1px,
          var(--text-color) 0px 0px 1px, var(--text-color) 0px 0px 1px,
          var(--text-color) 0px 0px 1px;
          }
      }
    }

    
  }
}

.yellowNavigation{
  background-color: var(--notFound-back-color)
}
.darkNav{
  background-color: rgba(0,0,0,.8);

  li{
filter: invert(100%);
  }
}

.theme {
  display: flex;
  gap: 10px;
  margin-bottom: 4rem;
  
  
  &__icon {
      width: 40px;
  height: 40px;
  border-radius: 50%;
      background-color: var(--very-slight-dark-color);
  cursor: pointer;
    &:hover path {
      fill: var(--dark-primary-color);
    }
    svg {
        display: block;
      width: 23px;
    }
  }
}
