/* ///////////////////////// */
@use "../../forwarding";
.works {
  position: relative;

  &__scrollTop {
    position: fixed;
    bottom: 1.5rem;
    z-index: 1;
    right: 1.5rem;
    svg {
      transform: rotate(-90deg);
    }
  }

  &__padd {
    padding: 3rem 0;
  }

  section {
    background-position: 50% 50%, 0 0;
    background-size: cover, auto;
    background-repeat: no-repeat;
  }
}

.item {
  height: 100vh;
  position: sticky;
  top: 0;
}
.top {
  height: 100vh;
  padding-top: clamp(1.8rem, 17rem, 23vh);


  .subtitle {
    margin-bottom: 3.6rem;
  }
}

.title {
  @include forwarding.stroked(
    $color: transparent,
    $strokeColor: #fff,
    $fontSize: clamp(4rem, 5rem, 6vh)
  );
  text-transform: uppercase;
  letter-spacing: .3rem;
}

.subtitle {
  text-transform: uppercase;
  font-size: 1.7rem;
}

.scrollDown {
  position: absolute;

  bottom: 2rem;
  left: 50%;
  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  font-size: 1.5rem;

  width: 13.5rem;
  cursor: pointer;
  &__arrow {
    font-size: 2rem;
  }
}

.cont {
  section:last-of-type {
    .visitSite {
      bottom: 1rem;
    }
  }

  .subtitle {
    font-size: 1.4rem;
  }
  .works__padd {
    height: 100%;

    & > div {
      height: 100%;
      position: relative;
      text-align: center;
    }
    .circle {
      padding: 1rem;
      width: 30rem;
      height: 30rem;
      border: 1px solid #fff;
      border-radius: 50%;
    }
  }

  .title {
    @include forwarding.stroked(
      $color: #000,
      $stroke: 2px,
      $strokeColor: #fff,
      $fontSize: clamp(2rem, 2rem, 3vh)
    );
  }
}

.visitSite {
  position: absolute;
  bottom: 5rem;
  left: 0;
}
