@use "../../forwarding";

.skills {
  position: relative;
  &__top {
    padding-top: clamp(1.8rem, 9rem, 13vh);
  }

  &__bottom {
    margin-top: clamp(1.8rem, 2rem, 6vh);
    display: flex;
    & > div {
      flex-basis: 50%;
    }
  }

  &__title {
    display: flex;
    column-gap: 1rem;
    flex-wrap: wrap;

    span,
    p {
      letter-spacing: 0.02em;
      text-transform: uppercase;
      @include forwarding.stroked($fontSize: clamp(4rem, 5rem, 6vh));
    }
  }
}

.detail {
  &__name {
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-size: 1.8rem;
    color: var(--dark-color);
  }

  &__txt {
    font-size: 1.4rem;
    color: var(--text-color);

    &:not(:last-of-type) {
      margin-bottom: 1rem;
    }
  }
}
.boxes {
  margin-top: -10rem;
  display: grid;
  grid-template-columns: repeat(3, 100px);
  justify-content: end;
  gap: 1.5rem;
}

@media (hover: hover) and (pointer: fine) {
  .boxes {
    gap: 2.5rem;
  }
}

.box {
  background-color: var(--primary-color);
  position: relative;
  overflow: hidden;
  padding: 0.4rem;
  aspect-ratio: 1/1;
  border-radius: 10px;
  cursor: help;

  span {
    transition: top 0.2s ease-in;
    position: absolute;
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-weight: 800;
    color: var(--dark-color);
    text-align: center;
    background-color: var(--slight-white-color);
  }
  &:hover span {
    top: 0;
  }
}
@media (hover: hover) and (pointer: fine) {
  .box {
    span {
      display: none;
    }
  }
}
.limits {
  position: relative;
  font-family: "Montserrat", Arial, sans-serif;
  font-size: calc(20px + 5vw);
  font-weight: 700;
  color: #fff;

  text-shadow: 0 0 0.15em #1da9cc;
  user-select: none;
  white-space: nowrap;
  filter: blur(0.007em);
  animation: shake 2.5s linear forwards;
}

.limits p {
  // position: absolute;
  // top: 0;
  // left: 0;
  // transform: translate(-50%, -50%);
  -webkit-clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
  clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
}

.limits::before,
.limits::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
}

.limits::before {
  animation: crack1 2.5s linear forwards;
  -webkit-clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
}

.limits::after {
  animation: crack2 2.5s linear forwards;
  -webkit-clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
  clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
}

@keyframes shake {
  5%,
  15%,
  25%,
  35%,
  55%,
  65%,
  75%,
  95% {
    filter: blur(0.018em);
    transform: translateY(0.018em) rotate(0deg);
  }

  10%,
  30%,
  40%,
  50%,
  70%,
  80%,
  90% {
    filter: blur(0.01em);
    transform: translateY(-0.018em) rotate(0deg);
  }

  20%,
  60% {
    filter: blur(0.03em);
    transform: translate(-0.018em, 0.018em) rotate(0deg);
  }

  45%,
  85% {
    filter: blur(0.03em);
    transform: translate(0.018em, -0.018em) rotate(0deg);
  }

  100% {
    filter: blur(0.007em);
    transform: translate(0) rotate(1.9deg);
  }
}

@keyframes crack1 {
  0%,
  95% {
    transform: translate(0%, 0%);
  }

  100% {
    transform: translate(-1%, 2%);
  }
}

@keyframes crack2 {
  0%,
  95% {
    transform: translate(0%, 0%);
  }

  100% {
    transform: translate(1%, -3%);
  }
}

/* /////////queries///////// */
@media (max-width: 1150px) {
  .skills {
    &__top {
      padding-top: clamp(1.8rem, 6rem, 7vh);
    }
    &__bottom {
      flex-direction: column-reverse;
      & > div {
        flex-basis: auto;
      }
    }
  }

  .detail {
    margin-top: clamp(1.8rem, 5rem, 6vh);
  }

  .boxes {
    margin-top: 0;
    justify-content: center;
    grid-template-columns: repeat(6, 100px);
    // justify-items: center;
  }
}
@media (max-width: 810px) {
  .skills {
    &__title {
      justify-content: center;
      text-align: center;
    }
  }
  .boxes {
    grid-template-columns: repeat(auto-fill, 100px);
  }
}

@media (max-width: 600px) {
  .detail {
    padding-bottom: 10rem;
  }
}

.floatedIcon {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: -1;
  transform: translate(-50%, -50%);

  svg {
    width: 70vw;
    min-height: 70vh;
    aspect-ratio: 1/1;
  }
}
