@mixin stroked(
  $stroke: 3px,
  $strokeColor: var(--dark-color),
  $color: var(--white-color),
  $fontSize: clamp(4rem, 9rem, 10vh),
  $fontWeight: 700
) {
  color: $color;
  font-size: $fontSize;
  font-weight: $fontWeight;
  font-family: "Comfortaa", cursive;
  text-shadow: $strokeColor 0px 0px $stroke, $strokeColor 0px 0px $stroke,
    $strokeColor 0px 0px $stroke, $strokeColor 0px 0px $stroke,
    $strokeColor 0px 0px $stroke, $strokeColor 0px 0px $stroke;

  span {
    position: absolute;
    left: -1rem;
    top: -1rem;
    font-size: 20px;
    text-shadow: $strokeColor 0px 0px 1px, $strokeColor 0px 0px 1px,
      $strokeColor 0px 0px 1px, $strokeColor 0px 0px 1px,
      $strokeColor 0px 0px 1px, $strokeColor 0px 0px 1px;
  }
}