// Tailwindcss Colors

$gray-400: hsl(57, 38%, 65%);
$gray-500: hsl(57, 38%, 46%);
$gray-600: hsl(57, 38%, 34%);
$gray-700: hsl(57, 38%, 27%);
$gray-800: hsl(58, 38%, 17%);
$gray-900: hsl(58, 38%, 29%);

$cyan-500: var(--primary-color);
$cyan-600: var(--primary-color);
$cyan-700: var(--dark-primary-color);
$cyan-800: var(--darkest-primary-color);


.app {
    background-color: $gray-900;
  width: 100vw;
  height: 100vh;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  .text_container {
        position: sticky;
    top: 12rem;
    z-index: 1;
    margin: 1rem 0;
    text-align: center;
    
    h1 {
      color: white; 
      font-family: 'Comfortaa', cursive;
      text-transform: uppercase;
      font-size: 5rem;
      margin-bottom: 1rem;
    }
    p {
      color: $gray-500;
      font-weight: 500;
      font-size: 1.4rem;
      letter-spacing: 0.05rem;
      margin: 0.5rem 0;
      color: $gray-400;
    }
    a {
      color: $cyan-500;
      
      &:hover {
        border-bottom: 0.2rem solid $cyan-500;
      }
    }
  }

  svg {
  background-color: $gray-700;
  width: 100%;
  overflow: visible;
  
  > path:nth-child(1) {
    fill: $gray-600;
    stroke: $gray-800;
  }
  
  > path:nth-child(2) {
    fill: $gray-900;
  }
  
  > path:nth-child(6) {
    fill: $gray-800;
  }
  
  text {
    fill: $gray-500;
    font-size: 660px;
    font-weight: 800;
    filter: drop-shadow(0 0 50px $gray-800);
  }
  
  > path:nth-child(8) {
    fill: $gray-900;
  }
}

.cactus {
  path:nth-child(1) {
    fill: $cyan-700;
  }
  path:nth-child(2) {
    fill: $cyan-800;
  }
  path:nth-child(3) {
    fill: black;
  }
}
}

